import { defineSupportCode } from 'cucumber';
import { reduceBy } from 'ramda';

defineSupportCode(({ When }) => {
  When(/^I get stats with parameters:$/, function (table) {
    const qs = reduceBy(
      (l, item) => l.concat(item.value), [],
      item => item.param,
      table.hashes());

    this.updateRequest({
      method: 'get',
      url: '/stats',
      qs,
    });
  });
});
